(function() {
	'use strict';

	angular
		.module('gpSocial', [
			'ngAnimate',
			'ngCookies',
			'ngTouch',
			'ngSanitize',
			'ngMessages',
			'ngAria',
			'ui.router',
			'ngMaterial',
			'toastr',
			'angAccordion'
		]);

})();

(function() {
  'use strict';

  angular
    .module('gpSocial')
    .service('profileService', profileService)

  /** @ngInject */
  function profileService($http) {
    this.getProfile = function() {
      return $http.get('https://fathomless-everglades-3680.herokuapp.com/api/user/1')
        .then(function(response) {
          return response.data;
        });
    };

    this.saveProfile = function(user) {
      return $http.post('https://fathomless-everglades-3680.herokuapp.com/api/user/1', user);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('gpSocial')
    .directive('socialNavbar', socialNavbar);

  /** @ngInject */
  function socialNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html'
    };

    return directive;
  }

})();
(function() {
  'use strict';

  angular
    .module('gpSocial')
    .controller('NavbarController', function ($scope, $timeout, $mdSidenav, $log) {
      $scope.toggleLeft = buildToggler('left');
       $scope.menuSections = [
        {
          name: 'My profile',
          state: {
            name: 'profile'
          }  
        },
        {
          name: 'Log Out',
          state: {
            name: 'login'
          }       
        }];       
     
      function buildToggler(navID) {
        return function() {
          $mdSidenav(navID)
            .toggle()
            .then(function () {
              $log.debug("toggle " + navID + " is done");
            });
        }
      }
    })

})();
(function() {
  'use strict';

  angular
    .module('gpSocial')
    .service('authorisationService', authorisationService)
    .service('authorisationServiceMock', authorisationServiceMock)
    .config(function($provide){
      $provide.decorator('authorisationService', function($delegate, authorisationServiceMock) {
        $delegate = authorisationServiceMock;
        return $delegate;
      });
    });

  /** @ngInject */
  function authorisationService($http) {
    this.login = function() {
      return $http.get('api/login');
    };

    this.logout = function() {
      return $http.get('api/logout');
    };
  }

  /** @ngInject */
  function authorisationServiceMock($http) {
    this.login = function() {
      return $http.get('app/components/authorisation/mock/login.mock.json');
    };

    this.logout = function() {
      return $http.get('app/components/authorisation/mock/logout.mock.json');
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('gpSocial')
    .controller('LoginController', LoginController);

    /** @ngInject */
    function LoginController(authorisationService, $log, $state){
      var vm = this;

      vm.enter = function() {
        $log.info("get");
        authorisationService.login().then(function(){
           $state.go('profile');
        });
      }
    }
})();

(function() {
  'use strict';

  angular
    .module('gpSocial')
    .controller('ProfileController', ProfileController);

  /** @ngInject */
  function ProfileController(profileService, $log){
    var vm = this;

    vm.genders = {
      M: 'Male',
      F: 'Female'
    };

    profileService.getProfile().then(function(user) {
      vm.user = user;
    });

    vm.save = function() {
      $log.info("save");
      vm.message = true; //временно
      profileService.saveProfile(vm.user).then(function(){
        /*vm.message = true;*/
      });
    }
  }
})();
(function() {
  'use strict';

  angular
    .module('gpSocial')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('gpSocial')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('login', {
        url: '/',
        templateUrl: 'app/login/login.html',
        controller: 'LoginController',
        controllerAs: 'login'
      })

      .state('profile', {
        url: '/profile',
        templateUrl: 'app/profile/profile.html',
        controller: 'ProfileController',
        controllerAs: 'profile'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('gpSocial')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('gpSocial')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

